<template>
  <app-back></app-back>
  <app-sets-item :item="setInfo" v-if="setInfo"></app-sets-item>

  <app-hint text="Помните о нагрузке, не используйте слишком большой для Вас вес"></app-hint>
  <app-hint text="Если у Вас возникнут вопросы по упражнению, обратитесь к дежурному тренеру спортзала"></app-hint>

  <app-exercise-item :exercises="setInfo.sets"
                v-if="setInfo"
                @show-description="onShowDescription"></app-exercise-item>

  <app-description-popup v-if="description && title"
                         :title="title"
                         :description="description"
                         @close-description="onCloseDescription"></app-description-popup>
</template>

<script>
import scrollLock from 'scroll-lock';

import StaticService from "@/services/static";
import AppBack from "@/components/Back";

import AppDescriptionPopup from "@/modules/set/components/DescriptionPopup";
import AppSetsItem from "@/components/SetsItem";
import AppExerciseItem from "@/components/ExerciseItem";
import AppHint from "@/components/Hint";

export default {
  name: 'AppSet',
  components: {
    AppHint,
    AppExerciseItem,
    AppSetsItem,
    AppDescriptionPopup,
    AppBack
  },
  data: () => ({
    setInfo: null,
    title: null,
    description: null,
  }),
  computed: {
    trainersSets() {
      return this.$store.state.trainersSets;
    },
    setGuid() {
      return this.$route.params.guid;
    },
  },
  mounted() {
    this.getTrainersSets();
    // this.getSet();
    // this.getTrainers();
  },
  methods: {
    onShowDescription(set) {
      console.log(set);
      console.log(set.description);

      scrollLock.disablePageScroll();
      this.title = set.title;
      this.description = set.description;
    },
    onCloseDescription() {
      scrollLock.enablePageScroll();
      this.description = null;
      this.title = null;
    },
    // getTrainers() {
    //   StaticService.getTrainers()
    //       .then(res => {
    //         this.$store.commit('setTrainersList', res);
    //         // this.trainerInfo = this.trainers.find((item) => item.guid === this.trainerGuid);
    //       })
    // },
    getTrainerSets() {
      StaticService.getTrainersSets()
          .then(res => {
            this.$store.commit('setTrainersSets', res);
            this.setInfo = this.trainersSets.find((item) => item.guid === this.setGuid);
          })
    },
    getTrainersSets() {
      if (this.trainersSets.length) {
        this.setInfo = this.trainersSets.find((item) => item.guid === this.setGuid);
      } else {
        this.getTrainerSets();
      }
    },
  }
}
</script>

