<template>
  <!--  TODO: обязательно надо добавить тренировки на несколько дней, продумать схему БД-->
  <div class="exercise" v-for="(exercise, index) in exercises" :key="index">
    <div class="exercise__title" :data-number="index + 1">
      {{ exercise.title }}
    </div>
    <div class="exercises-item__zones" v-if="exercise.zones && exercise.zones.length">
      Рабочая область:
      <span v-for="(zone, index) in exercise.zones" :key="index">
          {{ zone }}
        </span>
    </div>
    <div class="exercise__repeat">
      <b>{{ exercise.repeat }}</b> подходов по <b>{{ exercise.count }}</b> раз
    </div>
    <div class="exercise__weight" v-if="exercise.weight">
      Вес: {{ exercise.weight }}
    </div>

    <div class="exercise__show-buttons">
      <div class="exercise__button"
           @click="showDescription(exercise)">
        Хочу описание
      </div>
      <div class="exercise__button"
           v-if="exercise.videoFile"
           @click="showExample(exercise)">
        {{ !exercise.showExample ? 'Покажи пример' : 'Скрой пример' }}
      </div>
    </div>

    <div class="exercise__video" v-if="exercise.showExample">
        <app-player :src="require('@/assets/videos/' + exercise.videoFile)"></app-player>
    </div>
  </div>

</template>

<script>
import AppPlayer from "@/components/Player";

export default {
  name: 'AppExerciseItem',
  components: {AppPlayer},
  emits: ['showDescription'],
  props: {
    exercises: {
      type: Object,
      default: () => {
      }
    }
  },
  data: () => ({
  }),
  methods: {
    showExample(exercise) {
      exercise.showExample = !exercise.showExample;
    },
    showDescription(exercise) {
      this.$emit('showDescription', exercise);
    }
  }
}
</script>

