<template>
  <router-link :to="baseUrlPath + /set/ + item.guid" class="sets-item">
    <div class="sets-item__gym-img" v-if="item.club && item.club.photo">
      <img :src="require('@/assets/gyms/' + item.club.photo)"
           :alt="item.club.title + ' ' + item.club.street">
    </div>
    <div class="sets-item__info">
      <div class="sets-item__title">
        {{ item.title }}
      </div>
      <div class="sets-item__long">
        <b>{{ item.sets.length }}</b> упражнений на <b>~{{ item.time }}</b> минут
      </div>
      <div class="sets-item__zones" v-if="item.zones">
        <span v-for="zone, index in item.zones" :key="index">
          {{zone}}
        </span>
      </div>
      <div class="sets-item__trainer">
        Автор: <b>{{ getTrainerName(item.trainerGuid) }}</b>
      </div>
      <div class="sets-item__club" v-if="item.club.title || item.club.city">
        <b>{{ item.club.title }}</b> {{ item.club.city }}
      </div>
      <div class="sets-item__club" v-else>
        Для дома
      </div>
    </div>
  </router-link>
</template>

<script>
export default {
  name: 'AppSetsItem',
  props: {
    item: {
      type: Object,
      default: () => {
      }
    }
  },
  computed: {
    baseUrlPath() {
      return this.$store.state.baseUrlPath;
    },
    isTrainerPage() {
      return this.$route.name === 'Trainer';
    },
    trainersList() {
      return this.$store.state.trainersList;
    },
    // setGuid() {
    //   return this.$route.params.guid;
    // },
  },
  methods: {
    getTrainerName(guid) {
      if (this.trainersList.length) {
        return this.trainersList.find(item => item.guid === guid).name;
      } else {
        return '';
      }
    },
    getTrainerPhoto(guid) {
      if (this.trainersList.length) {
        return this.trainersList.find(item => item.guid === guid).photo;
      } else {
        return '';
      }
    }
  }
}
</script>

