<template>
  <video ref="videoPlayer" playsinline autoplay loop class="video-js vjs-big-play-centered vjs-touch-enabled">
    <source src="#" type="video/mp4">
  </video>

  <div class="video-controls">
    <div class="video-controls__btn" @click="skipToStart">
      В начало
    </div>
    <div class="video-controls__btn" @click="toggleVideoSpeed">
      Помедленней / Как обычно
    </div>
<!--    <div class="video-controls__btn" @click="exerciseVideoSpeedNormal">-->
<!--      Normal-->
<!--    </div>-->
  </div>
</template>

<script>
import videojs from 'video.js';

export default {
  name: 'AppPlayer',
  props: {
    options: {
      type: Object,
      default() {
        return {};
      }
    },
    src: {
      type: String,
      default: ''
    }
  },
  data: () => ({
    player: null,
    videoOptions: {
      nativeControlsForTouch: true,
      loop: true,
      autoplay: false,
      controls: true,
      muted: true,
      aspectRatio: '4:3',
      // liveui: true,
      playbackRates: [0.5, 1],
      preferFullWindow: false,
      options: {},
      // fluid: true,
      sources: [
      ],
      userActions: {
      }
    }
  }),
  mounted() {
    this.videoOptions.sources.push({
      src: this.src,
      type: 'video/mp4'
    })

    this.player = videojs(this.$refs.videoPlayer, this.videoOptions, () => {
      this.player.log('onPlayerReady', this);
    });
  },
  methods: {
    skipToStart() {
      this.player.currentTime(0);
    },
    toggleVideoSpeed() {
      if (this.player.playbackRate() === 1) {
        this.player.playbackRate(0.5);
      } else {
        this.player.playbackRate(1);
      }
    },
  },
  beforeUnmount() {
    if (this.player) {
      this.player.dispose();
    }
  }
}
</script>

