import { createStore } from 'vuex';
import settings from '../settings';

const store = createStore({
  state: {
    baseUrlPath: process.env.NODE_ENV === "production" ? settings.apiEndpointGitHub : settings.apiEndpoint,

    userName: '',
    trainersList: [],
    trainersSets: [],
    trainersSetsMale: [],
    trainersSetsFemale: [],
    citiesList: [],
    selectedCityGuid: 'city-1',
    articlesList: [],
  },
  mutations: {
    setTrainersList(state, data) {
      state.trainersList = data;
    },
    setTrainersSets(state, data) {
      state.trainersSets = data;
    },
    setTrainersSetsMale(state, data) {
      state.trainersSetsMale = data;
    },
    setTrainersSetsFemale(state, data) {
      state.trainersSetsFemale = data;
    },
    setCitiesList(state, data) {
      state.citiesList = data;
    },
    setSelectedCityGuid(state, data) {
      state.selectedCityGuid = data;
    },
    setArticlesList(state, data) {
      state.articlesList = data;
    }
  },
  getters: {
    getUserName: state => state.userName,
    getTrainersList: state => state.trainersList,
    getTrainersSets: state => state.trainersSets,
  },
  actions: {

  },
  modules: {

  }
});

export default store;
